@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  padding: 20px 0;
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}

.container {
  width: 320px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.btn {
  position: absolute;
  right: 0;
  border: none;
  font-family: inherit;
  padding: 5px 30px;
  height: 100%;
  cursor: pointer;
  text-transform: uppercase;
  background: #0097e6;
  color: #333333;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn:hover {
  background: #095e8b;
}

.btn:disabled {
  background: #7294a7;
}

.input {
  height: 30px;
  margin-right: 10px;
  width: 100%;
  border: none;
  outline: none;
}
.input__container {
  display: flex;
  position: relative;
}

.timer {
  display: flex;
  margin-top: 25px;
  background: #bdc3c7;
  background: #d3cce3;
  background: -webkit-linear-gradient(to right, #e9e4f0, #d3cce3);
  background: linear-gradient(to right, #e9e4f0, #d3cce3);
  height: 110px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70px;
  height: 70px;
  color: #333333;

  border-radius: 10px;
  margin-right: 10px;
}

.value {
  font-weight: 500;
  font-size: 50px;
}

.field:not(:last-child)::after {
  display: block;
  content: ':';
  margin-top: -72px;
  margin-right: -80px;
  font-weight: bold;
  color: #333333;
}
